ent: space-between;\n  align-items: flex-end;\n}\n\n.list .goods .goodsDetail .detailFl {\n  display: flex;\n  justify-content: center;\n  align-items: baseline;\n}\n\n.list .goods .goodsDetail .detailFl b {\n  color: #ff4862;\n  font-size: 0.533333rem;\n}\n\n.list .goods .goodsDetail .detailFl b span {\n  font-size: 0.373333rem;\n}\n\n.list .goods .goodsDetail .detailFl .quan {\n  width: 0.613333rem;\n  height: 0.4rem;\n  margin-left: 0.133333rem;\n  background: url(\"../../imgs/index/icon-quan.png\") no-repeat center/100%;\n}\n\n.list .goods .goodsDetail .detailFl span {\n  font-size: 0.32rem;\n}\n\n.list .goods .goodsDetail .detailFl .tuanNum {\n  color: #333;\n  margin-left: 0.266667rem;\n}\n\n.list .goods .goodsDetail .detailFl .singlePrice {\n  margin-left: 0.4rem;\n  color: #999;\n}\n\n.list .goods .goodsDetail .detailFl .singlePrice span {\n  color: #333;\n}\n\n.list .goods .goodsDetail .detailFr {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.list .goods .goodsDetail .detailFr .groupingList {\n  display: flex;\n  justify-content: flex-start;\n  margin-right: 0.266667rem;\n}\n\n.list .goods .goodsDetail .detailFr .groupingList img {\n  display: block;\n  width: 0.72rem;\n  height: 0.72rem;\n  border: 0.013333rem solid #ccc;\n  -webkit-border-radius: 50%;\n  -moz-border-radius: 50%;\n  border-radius: 50%;\n}\n\n.list .goods .goodsDetail .detailFr .groupingList img:nth-child(2) {\n  margin-left: -0.186667rem;\n}\n\n.list .goods .goodsDetail .detailFr .goDetailBtn {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background: linear-gradient(to right, #ff8484, #ff464e);\n  -webkit-border-radius: 0.133333rem;\n  -moz-border-radius: 0.133333rem;\n  border-radius: 0.133333rem;\n  font-size: 0.373333rem;\n  width: 2.16rem;\n  line-height: 0.8rem;\n}\n\n.list .goods .goodsDetail .detailFr .goDetailBtn i {\n  width: 0.133333rem;\n  height: 0.24rem;\n  margin-left: 0.133333rem;\n  background: url(\"../../imgs/inde